/** Media Queries **/
html {
  font-size: 62.5%;
  box-sizing: border-box;
  scroll-behavior: smooth;
  scroll-snap-type: y mandatory;
  background-color: #000234;
}

*, *:before, *:after {
  box-sizing: inherit;
}

body {
  font-family: "Poppins", sans-serif;
  font-size: 1.6rem;
  line-height: 1.8;
  display: flex;
  flex-direction: column;
  align-items: center;
}

.background {
  width: 100%;
  background-repeat: no-repeat;
  background-size: cover;
  background-position: 0 0;
}

.webp .background {
  background-image: url(/build/img/bg.webp);
}

.no-webp .background {
  background-image: url(/build/img/bg.jpg);
}

p {
  color: #FFFFFF;
  font-size: 2rem;
}

.contenedor {
  width: 100%;
  height: 100vh;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  scroll-snap-align: center;
  scroll-snap-stop: always;
}

.contenido {
  display: flex;
  width: 100%;
  justify-content: space-around;
  align-items: center;
}

.btn {
  background-color: #B90000;
  color: #FFFFFF;
  font-weight: 700;
  text-decoration: none;
  padding: 1rem 3rem;
  text-align: center;
  margin-top: 1.25rem;
  display: block;
  border: none;
  cursor: pointer;
  border-radius: 0.5rem;
  transition: all 0.3s;
  font-size: 1.5rem;
}
.btn:hover {
  background-color: rgba(0, 0, 0, 0);
  box-shadow: inset 0 0 0 3px #B90000;
  color: #B90000;
}

.owl-carousel {
  height: auto;
}

a {
  text-decoration: none;
  color: #FFFFFF;
}

span {
  color: #FFFFFF;
}

img,
picture {
  width: 100%;
  display: block;
}

h1, h2, h3, h4 {
  margin: 0;
  color: #FFFFFF;
}

h1 {
  font-size: 3.8rem;
}

h2 {
  font-size: 2.5rem;
  margin: 3rem 0;
}
@media (min-width: 480px) {
  h2 {
    font-size: 3.4rem;
  }
}

h3 {
  font-size: 3rem;
  margin: 2rem 0;
}

h4 {
  font-size: 2.6rem;
}

.main {
  width: 100%;
  height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: stretch;
}
.main .contenedor {
  padding-left: 20rem;
  align-items: flex-start;
}
.main .contenedor h1 {
  font-size: 6rem;
}
.main .contenedor span {
  display: block;
  font-size: 3rem;
}

.navegacion {
  width: 100%;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  position: fixed;
  width: 100%;
  height: 100%;
  background: linear-gradient(-135deg, #000234, #B90000);
  clip-path: circle(25px at calc(100% -45px) 45px);
  transition: all 0.3s ease-in-out;
  z-index: 9;
}
.navegacion a {
  font-weight: 700;
  color: #FFFFFF;
  position: relative;
  display: flex;
  flex-direction: column;
  height: auto;
  justify-content: center;
  align-items: center;
  margin-top: 3rem;
  padding: 0.5rem;
}
.navegacion a:after {
  content: "";
  width: 120%;
  height: 3rem;
  background: rgba(255, 255, 255, 0.5);
  position: absolute;
  border-radius: 50px;
  transform: scaleY(0);
  transition: 0.3s;
  padding: 2rem;
  z-index: -1;
}
.navegacion a:hover:after {
  transform: scaleY(1);
}
.navegacion a:hover {
  color: #000227;
}

#active:checked ~ .navegacion {
  clip-path: circle(75%);
}

.header input {
  display: none;
}
.header .btn-menu {
  position: fixed;
  right: 2rem;
  top: 2rem;
  z-index: 10;
  text-align: center;
  line-height: 5rem;
  height: 5rem;
  width: 5rem;
  background: #B90000;
  border-radius: 50%;
  color: #000234;
  font-size: 2rem;
  cursor: pointer;
  transition: all 0.3s ease;
}

#active:checked ~ .btn-menu {
  background: #FFFFFF;
}

#active:checked ~ .btn-menu i:before {
  content: "";
}

.btn-dark {
  font-size: 3rem;
}

#sobre-mi {
  background-color: rgba(0, 2, 52, 0.5);
}
#sobre-mi p {
  width: 50%;
  text-align: center;
}

.tecnologia {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  margin: 0 2rem;
  width: auto;
}
.tecnologia p {
  font-size: 1.8rem;
  color: rgba(255, 255, 255, 0.5);
  margin: 0;
  text-align: center;
}

.icon {
  width: 3rem;
  display: flex;
  justify-content: center;
  align-items: center;
}
.icon i {
  font-size: 3rem;
  color: rgba(255, 255, 255, 0.5);
}

.owl-carousel .owl-dots {
  display: none;
}

.contenedor-proyectos {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  width: 100%;
  overflow: hidden;
}

.proyectos {
  width: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
  flex-wrap: nowrap;
}

.owl-nav {
  display: flex;
  justify-content: center;
  align-items: center;
}
.owl-nav i {
  display: flex;
  justify-content: center;
  align-items: center;
  width: 5rem;
  height: 5rem;
  font-size: 4rem;
  box-sizing: border-box;
  margin: 0 2rem;
  background-color: #B90000;
  border-radius: 50%;
  color: #FFFFFF;
}
.owl-nav i:hover {
  background-color: rgba(0, 0, 0, 0);
  box-shadow: inset 0 0 0 3px #B90000;
  color: #B90000;
}
.owl-nav .disabled {
  visibility: hidden;
}

.card {
  background-color: #131440;
  box-shadow: 0px 10px 20px #000000;
  overflow: hidden;
  border-radius: 1rem;
  width: 32rem;
  height: 25rem;
  transition: 0.4s;
  margin: 3rem;
  position: relative;
  display: flex;
  flex-direction: column;
  justify-content: center;
}
.card .portada-proyecto {
  height: 100%;
  position: absolute;
  transition: 0.4s;
  display: flex;
  flex-direction: column;
  justify-content: center;
}
.card .portada-proyecto h3 {
  font-size: 2rem;
  margin: 3rem 0;
  line-height: 2.5rem;
  text-align: center;
}
.card .titulo-proyecto {
  height: 40%;
  display: flex;
  justify-content: center;
  align-items: center;
  flex-grow: 1;
  padding: 1rem;
}
.card .descripcion-proyecto {
  opacity: 0;
  transition: 0.5s;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
}
.card:hover .portada-proyecto {
  transform: translateY(-100%);
  opacity: 0;
}
.card:hover .descripcion-proyecto {
  opacity: 1;
}
.card .btn {
  margin: 1rem;
  font-size: 1rem;
  background: #B90000;
  color: #FFFFFF;
  font-weight: 700;
  text-decoration: none;
  padding: 1rem 3rem;
  text-align: center;
  margin-top: 1.25rem;
  display: block;
  border: none;
  cursor: pointer;
  border-radius: 0.5rem;
  transition: 0.5s;
  padding: 0.5rem 1rem;
}
.card .btn:hover {
  transform: scale(0.9);
}
.card p {
  padding: 1rem;
  font-size: 1.2rem;
  text-align: center;
}

.contenedor-contacto {
  display: flex;
  justify-content: space-between;
  align-items: center;
  width: 100%;
  height: 100%;
  padding-top: 10rem;
}

.formulario {
  display: flex;
  width: 65%;
  height: 100%;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  background-color: #B90000;
  box-shadow: 20px -20px 50px #000000;
  padding: 1.5rem;
  border-radius: 0 2rem 0 0;
}
.formulario input, .formulario textarea {
  display: block;
  margin-bottom: 2rem;
  border: none;
  border-radius: 0.5rem;
  padding: 0.5rem;
  width: 100%;
}
.formulario textarea {
  resize: none;
}
.formulario input:last-of-type {
  margin-bottom: 0;
}
.formulario fieldset {
  color: #FFFFFF;
  border: 0.1rem solid #FFFFFF;
  border-radius: 1rem;
  padding: 2rem;
  width: 100%;
}
.formulario fieldset .btn {
  background: #000234;
  color: #FFFFFF;
  font-weight: 700;
  text-decoration: none;
  padding: 1rem 3rem;
  text-align: center;
  margin-top: 1.25rem;
  display: block;
  border: none;
  cursor: pointer;
  border-radius: 0.5rem;
  transition: 0.5s;
}
.formulario fieldset .btn:hover {
  transform: scale(0.9);
}
.formulario p {
  margin: 2rem 0 0 0;
  font-size: 1.5rem;
}

.inputs {
  display: flex;
  flex-direction: column;
  width: 100%;
}

.nombre-apellido {
  display: flex;
  flex-direction: column;
}
.nombre-apellido input {
  display: inline-flex;
}
.nombre-apellido input:first-child {
  margin-right: 2rem;
}
.nombre-apellido input:last-of-type {
  margin-bottom: 2rem;
}
@media (min-width: 768px) {
  .nombre-apellido {
    flex-direction: row;
    justify-content: space-between;
  }
}

.info-contacto {
  width: 35%;
  height: 100%;
  text-align: center;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}
.info-contacto p {
  margin: 0;
}
.info-contacto a {
  font-size: 2rem;
}

.enlaces-contacto {
  display: flex;
  justify-content: center;
}
.enlaces-contacto a {
  color: #FFFFFF;
  font-size: 3rem;
  margin-right: 2rem;
}
.enlaces-contacto a:last-of-type {
  margin-right: 0;
}

.link-contacto {
  display: block;
}

.alerta {
  display: block;
  padding: 0.5rem;
  border-radius: 1rem;
  color: #e1e1e1;
  font-weight: 700;
  margin-bottom: 1rem;
}

.error {
  background-color: #b91a1a;
  border: 0.3rem solid #8c1414;
}

.success {
  background-color: #0f8a34;
  border: 0.3rem solid #0a5c23;
}